.App-header {
  background-color: #EA5858;
  min-height: 100vh;
}

.topbar{
   width: 100%;
   position: fixed;
   z-index: 1;
   top: 0;
   overflow-y: hidden;
   text-align: left;
   background-color: #777777;
   padding:5px;
}

.sidebar{
   height: 100%;
   width: 11%;
   position: fixed;
   z-index: 1;
   top: 0;
   left: 0;
   overflow-x: hidden;
   background-color: #e1e1d0;
   padding-top: 20px;
}

.links{
   color: black;
   text-decoration: none;
   margin: 10px;
   padding-top: 10px;
   padding-bottom: 10px;
   font-size: 1.25em;
}

.links:hover{
   text-decoration: underline;
}

.links:visited{
   text-decoration: none;
}

.links:active{
   font-weight: bold;
}
